import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'
import index from '@/pages/index/index'//
import Bindphone from '@/pages/bindphone'//
import Register from '@/pages/register'//
import Coupons from '@/pages/coupons'//
import notices from '@/pages/notices'  //
import mypublish from '@/pages/mypublish'//
import threaddetail from '@/pages/threaddetail'//
import addresses from '@/pages/addresses'  //

// import common from '../../static/js/common.js'
Vue.use(Router)

let router= new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      children:[
        {path:'',component:index,meta:{title:''}}
      ]
    },
    //
    // {
    //   path:'',
    //   name:'index',
    //   component:index,
    //   meta: {
    //     title:''
    //   }
    // },
    {
      path:'/register',
      name:'register',
      component:Register,
      meta: {
        title:''
      }
    },
    {
      path:'/bindphone',
      name:'bindphone',
      component:Bindphone,
      meta: {
        title:''
      }
    },
    {
      path:'/coupons',
      name:'coupons',
      component:Coupons,
      meta:{
        title:''
      }
    }, 
    {
      path:'/notices',
      name:'notices',
      component:notices,
      meta:{
        title:''
      }
    },
    {
      path:'/mypublish',
      name:'mypublish',
      component:mypublish,
      meta:{
        title:''
      }
    },
    {
      path:'/threaddetail',
      name:'threaddetail',
      component:threaddetail
    },
    {
      path:'/addresses',
      name:'addresses',
      component:addresses,
      meta:{
        title:''
      }
    }
 
  ]
})

////
// to  from next() 
router.beforeEach((to, from, next) => {
  //title
  // console.log(to.matched[0].name)
  // if(to.matched[0].name !== "index") {
  //   // common.checkRegisterStatus()
  // }

  changeTitle(to.meta.title);
  next();
});

//title
function changeTitle(title) {
  title = title ? title : '';
  window.document.title = title;
};


export default router



